import React, {Component} from 'react'

export default class VoteBlock extends Component{
	constructor(props){
		super(props)
		this.state = {
			count : 0
		}
	}

	handleClick = () => {
			this.state.count++;
			this.setState(this.state);
		}
	render(){

		let {index,language } = this.props
		return (
			<tr>
				<td style={{borderBottom:"1px solid black", width: '10%'}}>{this.state.count}</td>
				<td style={{borderBottom:"1px solid black", width: '20%'}}>{language}</td>
				<td style={{borderBottom:"1px solid black", width: '10%'}}><a href='javascript:void(0)' onClick={this.handleClick}>Click here</a></td>
				
			</tr>
		);
	}

}