import React, { Component } from 'react'
import Symbol from './Symbol'
import './index.css';

export default class TicTacToe extends Component {

	constructor(props) {
		super(props);
		this.state = {
			array: [
				[-1, -1, -1],
				[-1, -1, -1],
				[-1, -1, -1],
			],
			firstUser: true
		}

	}

	showWinner = (val) => {
		if (val === 0) {
			alert("Player 1 is the winner")
		}
		else if (val === 1) {
			alert("Player 2 is the winner")
		}
	}

	checkWinner = () => {
		let arr = this.state.array;
		for (let i = 0; i < 3; i++) {
			if ((arr[i][0] === 0 && arr[i][1] === 0 && arr[i][2] === 0)
				||
				(arr[0][i] === 0 && arr[1][i] === 0 && arr[2][i] === 0)) {
				this.showWinner(0);
				return true;
			}
			else if ((arr[i][0] === 1 && arr[i][1] === 1 && arr[i][2] === 1)
				||
				(arr[0][i] === 1 && arr[1][i] === 1 && arr[2][i] === 1)){
				this.showWinner(1);
				return true;
			}

			if((arr[0][0] === 0 && arr[1][1] === 0 && arr[2][2] === 0)
			|| (arr[0][2] === 0 && arr[1][1] === 0 && arr[2][0] === 0)){
				this.showWinner(0);
				return true;

			}
			if((arr[0][0] === 1 && arr[1][1] === 1 && arr[2][2] === 1)
			|| (arr[0][2] === 1 && arr[1][1] === 1 && arr[2][0] === 1)){
				this.showWinner(1);
				return true;

			}
		}
	}

	render(){
		return (
			<table>
				<tbody>
					{
						this.state.array.map((item, idx) => {
							return (
								<tr>
									{
										item.map((col, i) => {
											return (
												<td onClick={() => {
													let arr = this.state.array;
													if (arr[idx][i] !== -1) {
														alert("Already selected. Select other one");
														return;
													}
													arr[idx][i] = this.state.firstUser ? 0 : 1;
													this.setState({
														array: arr,
														firstUser: !this.state.firstUser
													})
													if(this.checkWinner()){
														this.setState({
															array: [
																[-1, -1, -1],
																[-1, -1, -1],
																[-1, -1, -1],
															],
															firstUser: true
													})
													}
												}}><Symbol val={col} /></td>
											)
										})
									}
								</tr>
							)
						})
					}
				</tbody>
		</table>
		);
	}
}