import React from 'react';
import logo from './logo.svg';
import './App.css';
import {BrowserRouter as Router, Route, Switch, Link, Redirect} from "react-router-dom"
import MainPage from "./pages"
import Calculator from "./pages/Calculator"
import NotFoundPage from "./pages/404"
import UsersList from "./pages/users"
import FullPage from "./pages/FullPage"
import Header from "./pages/header"
import VoteYourLanguage from "./pages/VoteYourLanguage"
import TicTacToe from "./pages/TicTacToe"
import WebPage from './pages/WebAppWithNavBar/WebPage';

function App() {
  return (
    <Router>
		<Switch>
			<Route exact path="/my-app/" component={MainPage}></Route>
			<Route exact path ="/my-app/404" component={NotFoundPage}/>
			<Route exact path ="/my-app/users" component={UsersList}/>
			<Route exact path ="/my-app/fullPage" component={FullPage}/>
			<Route exact path ="/my-app/header" component={Header}/>
			<Route exact path ="/my-app/calculator" component={Calculator}/>
			<Route exact path ="/my-app/vote" component={VoteYourLanguage}/>
			<Route exact path ="/my-app/tic" component={TicTacToe}/>
			<Route exact path ="/my-app/VrindavanBabajis" component={WebPage}/>
			<Redirect to="/404"/>
		</Switch>
	</Router>
  );
}

export default App;
