import React, { Component } from 'react'
import './index.css';
import ButtonGroup from "./buttonGroup"
import CalcButton from "./button"
import { textAlign } from '@material-ui/system';

export default class Calculator extends Component {

	constructor(props) {
		super(props);
		
		this.state = {
			result: 0,
			operation: (a) => a,
			complete : true
		};
	}

	handleInput = (buttonValue) => {
			// for + and -

		let {result} = this.state;
		
		if (isNaN(buttonValue)) {
			result = this.state.operation(result);
			this.setState({complete : false});
			if(buttonValue === "+"){
				this.setState({operation: (a) => result + a});
			}
			else if(buttonValue === "-"){
				this.setState({operation: (a) => result - a});
			}
			else if(buttonValue === "*"){
				this.setState({operation: (a) => result * a});
			}
			else if(buttonValue === "/"){
				this.setState({operation: (a) => result / a});
			}
			else if(buttonValue === "="){
				this.state.operation(result)
				this.setState({operation: (a) => a});
			}
			else if(buttonValue === "C"){
				this.setState({operation: (a) => a});
				result = 0;
			}
			
		}
		else {
			if(!this.state.complete){
				result = 0;
				this.setState({complete : true});
			}
			result = result * 10;
			if(buttonValue !== 0){
				result = result + buttonValue;
			}
		}
		this.setState({result:result});
	}


	render() {
		return (
			<div style={{textAlign:'center' , width: '100%', backgroundColor: 'beige', height: '100vh'}}>
				<h2>CALCULATOR</h2>
				<input type='text' id="resultDisplay" style={{ width: "20%", height:'30px', fontSize:'20px', fontWeight:'bold' }} value={this.state.result} defaultValue={0} />
				<ButtonGroup>
					<CalcButton class="ButtonClass" handleInput={this.handleInput} buttonVal={1} {...this.state} />
					<CalcButton class="ButtonClass" handleInput={this.handleInput} buttonVal={2} {...this.state} />
					<CalcButton class="ButtonClass" handleInput={this.handleInput} buttonVal={3} {...this.state} />
					<CalcButton class="ButtonClass" handleInput={this.handleInput} buttonVal="C" {...this.state} />
				</ButtonGroup>
				<ButtonGroup>
					<CalcButton class="ButtonClass" handleInput={this.handleInput} buttonVal={4} {...this.state} />
					<CalcButton class="ButtonClass" handleInput={this.handleInput} buttonVal={5} {...this.state} />
					<CalcButton class="ButtonClass" handleInput={this.handleInput} buttonVal={6} {...this.state} />
					<CalcButton class="ButtonClass" handleInput={this.handleInput} buttonVal="*" {...this.state} />
				</ButtonGroup>
				<ButtonGroup>
					<CalcButton class="ButtonClass" handleInput={this.handleInput} buttonVal={7} {...this.state} />
					<CalcButton class="ButtonClass" handleInput={this.handleInput} buttonVal={8} {...this.state} />
					<CalcButton class="ButtonClass" handleInput={this.handleInput}  buttonVal={9} {...this.state} />
					<CalcButton class="ButtonClass" handleInput={this.handleInput} buttonVal="/" {...this.state} />
				</ButtonGroup>
				<ButtonGroup>
					<CalcButton class="ButtonClass" handleInput={this.handleInput} buttonVal={0} {...this.state} />
					<CalcButton class="ButtonClass" handleInput={this.handleInput} buttonVal="+" {...this.state} />
					<CalcButton class="ButtonClass" handleInput={this.handleInput} buttonVal="-" {...this.state} />
					<CalcButton class="ButtonClass" handleInput={this.handleInput} buttonVal="=" {...this.state} />
				</ButtonGroup>

			</div>
		)
	};

}