import React, {Component} from 'react'
import VoteBlock from "./voteBlock"

export default class VoteYourLanguage extends Component{
	constructor(props){
		super(props);
	}
	render(){
		const NO_OF_LANGS = 5;
		
		
		return(
			<div style={{textAlign:'center'}}>
				<h2>Vote for your language</h2>
				<table style={{border:"1px solid black", backgroundColor:"pink", width:'60%', marginLeft:'auto', marginRight:'auto'}}>
					<tbody>
					{
						["Python", "Java", "Javascript", "React", "HTML"].map((item, idx) => {
							return <VoteBlock language={item} />
						})
					}
					</tbody>
				</table>
			</div>
		)
	}
}