import React, {Component} from 'react'

class Header extends Component{
	render(){
		let h = this.props.height || 100;
		let w = this.props.width || 300;
		
		return (
			<div style={{
				backgroundColor: "black",
				width: `${w}px`,
				height: `${h}px`,
				display: 'flex',  
				justifyContent:'center', 
				alignItems:'center',
				fontSize:"20px",
				color: "white"
			}}>
				React Masterminds
			</div>
		);
	}
}
export default Header;