import React, { Component } from 'react'
import {Button} from '@material-ui/core'


export default class CalcButton extends Component {
	render() {
		const { buttonVal, label, handleInput } = this.props;
		return <Button variant="outlined" color="primary" className="buttonClass"
			value={buttonVal}
			onClick = {() => handleInput(buttonVal)}>
				{label == undefined ? buttonVal : label}
		</Button>

		
	}

	
}